.mf-home-application__card {
  flex: 0 0 25%;
  padding: 10px 20px;
  padding-bottom: 30px;
  cursor: pointer;
}
.mf-home-application__card img {
  width: 80px;
  height: 80px;
}
.mf-home-application__card .mf-home-application__card__text {
  display: inline-block;
  width: 60%;
  vertical-align: top;
  margin-left: 20px;
}
.mf-home-application__card .mf-home-application__card__text .mf-home-application__card__title {
  font-size: 14px;
  color: #333333;
  letter-spacing: 0.14px;
  margin-top: 10px;
  font-weight: bold;
}
.mf-home-application__card .mf-home-application__card__text .mf-home-application__card__desc {
  font-size: 12px;
  color: #999999;
  letter-spacing: 0;
  margin-top: 6px;
}
@media screen and (max-width: 1400px) {
  .mf-home-application__card {
    flex: 0 0 33.3%;
  }
}
