.mf-home-application {
  background: #ffffff;
  margin: 20px;
  box-shadow: 0 0 15px 0 rgba(138, 174, 254, 0.2);
  border-radius: 2px;
  padding: 0px 30px;
}
.mf-home-application .mf-home-application__title {
  font-size: 20px;
  color: #333333;
  letter-spacing: 0.19px;
  line-height: 70px;
}
.mf-home-application .mf-home-application__subtitle {
  line-height: 60px;
  font-size: 18px;
  color: #333333;
  letter-spacing: 0.18px;
}
.mf-home-application .mf-home-application__apps {
  min-height: 120px;
  display: flex;
  flex-wrap: wrap;
}
